body,
div,
a,
h4 {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a,
a:visited,
a:hover,
a:focus,
a:active {
  outline: none;
  text-decoration: none;
}

img {
  vertical-align: top;
}

html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-weight: 400;
  line-height: 1;
  font-size: 12px;
  background-color: #e5e5e5;
}
